export default {
	data() {
		return {
			currentNumber: 0, // 用来判断active样式类是否显示
			statusChange: '',
			curPage: 1,
			status: 0,
			nameList: [],
			id: '',
			acid:'',
			list: [{
					title: "全部"
				},
				{
					title: "充值"
				},
				{
					title: "提现"
				}
			]
		};
	},
	onLoad(options) {
		if(!uni.getStorageSync("token")&&!uni.getStorageSync("SecretKey")){
			this.$base._isLogin()
		}
		this.getCoreDetail()
	},
	onPullDownRefresh() {
		this.getCoreDetail()
		  setTimeout(function () {
			  uni.stopPullDownRefresh();
		  }, 1000);
	  },
	methods: {
		currentInfo(index) {
			this.currentNumber = index;
			console.log(this.currentNumber)
			this.status = this.currentNumber
			//我的理财列表动态 显示
			uni.request({
				url: this.baseUrl + "/investment-list?status=" + this.status,
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token'),
					SecretKey: uni.getStorageSync('SecretKey')
				},
				success: (res) => {
					console.log(res)
					 if (res.data.status == 1) {
						this.nameList = res.data.data.data
						console.log(this.nameList)
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		},
		//我的理财列表 显示全部
		getCoreDetail() {
			uni.request({
				url: this.baseUrl + "/recharge-withdraw",
				data:{
					page:1,
					count:10000
				},
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res.data)
					if (this.$base._indexOf(res.data.status)) {
						this.$base._isLogin()
					} else  if (res.data.status == 1) {
						// this.nameList = res.data.data.data
						
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		},
		showState(state){
			if(state==1){
				return  "待理财";
			}else if(state==2){
				return "理财中"
			}else if(state==3){
				return "已撤资"
			}else if(state==4){
				return "已完结"
			}
		},
		jumpToRecorde(index) {
			uni.navigateTo({
				url: "./recorder-detail?id="+this.nameList[index].Id
			})
		}
	}
}
